import React, { useCallback, useEffect } from "react";
import MainCard from "ui-component/cards/MainCard";
import InputLabel from "ui-component/extended/Form/InputLabel";
import { gridSpacing } from "store/constant";
import { useParams } from "react-router-dom";
import { toast } from "react-hot-toast";
import MemberApi from "apis/member-list.api";
import { Grid, Stack, TextField, MenuItem, Select } from "@mui/material";
import { useSelector } from "react-redux";

function ViewMember() {
  const params = useParams();
  const memberApi = new MemberApi();
  const [name, setName] = React.useState("");
  const [gender, setGender] = React.useState("");
  const [fatherName, setFatherName] = React.useState("");
  const [voterId, setVoterId] = React.useState("");
  const [boothId, setBoothId] = React.useState("");
  const [wardNumber, setWardNumber] = React.useState("");
  const [dob, setDOB] = React.useState("");
  const [whatsappNumber, setWhatsappNumber] = React.useState("");
  const [number, setNumber] = React.useState("");
  const [voterView, setVoterView] = React.useState("");
  const [occupation, setOccupation] = React.useState("");
  const [occupationCity, setOccupationCity] = React.useState("");
  const [education, setEducation] = React.useState("");
  const [caste, setCaste] = React.useState("");
  const [voted, setVoted] = React.useState("");
  const [dead, setDead] = React.useState("");
  const booths = useSelector((state) => state.booth.Booth);

  const getMemberById = useCallback(async () => {
    try {
      const getMemberByIdResponse = await memberApi.getMemberById({
        voterId: params.id,
      });
      if (getMemberByIdResponse && getMemberByIdResponse?.data?.code === 200) {
        setName(getMemberByIdResponse?.data?.data?.name);
        setFatherName(getMemberByIdResponse?.data?.data?.fatherName);
        setVoterId(getMemberByIdResponse?.data?.data?.voterId);
        setBoothId(getMemberByIdResponse?.data?.data?.boothId.boothId);
        setGender(getMemberByIdResponse?.data?.data?.gender);
        setWardNumber(getMemberByIdResponse?.data?.data?.familyId.wardNumber);
        setDOB(getMemberByIdResponse?.data?.data?.dob);
        setWhatsappNumber(getMemberByIdResponse?.data?.data?.whatsappNumber);
        setNumber(getMemberByIdResponse?.data?.data?.number);
        setVoterView(getMemberByIdResponse?.data?.data?.voterView);
        setOccupation(getMemberByIdResponse?.data?.data?.occupation);
        setOccupationCity(getMemberByIdResponse?.data?.data?.occupationCity);
        setEducation(getMemberByIdResponse?.data?.data?.education);
        setCaste(getMemberByIdResponse?.data?.data?.caste);
        setVoted(getMemberByIdResponse?.data?.data?.voted);
        setDead(getMemberByIdResponse?.data?.data?.dead);
      } else {
        return toast.error(`Something went wrong!`);
      }
    } catch (error) {
      console.error(error);
      toast.error("Something went wrong");
      throw error;
    }
  });

  useEffect(() => {
    getMemberById();
  }, []);

  return (
    <MainCard>
      <form action="#">
        <Grid container spacing={gridSpacing}>
          <Grid item xs={12} md={6}>
            <Stack>
              <InputLabel required>Name</InputLabel>
              <TextField
                fullWidth
                id="name"
                name="name"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
                disabled
              />
            </Stack>
          </Grid>
          <Grid item xs={12} md={6}>
            <Stack>
              <InputLabel required>Guardian Name</InputLabel>
              <TextField
                fullWidth
                id="fatherName"
                name="fatherName"
                value={fatherName}
                onChange={(e) => {
                  setFatherName(e.target.value);
                }}
                disabled
              />
            </Stack>
          </Grid>
          <Grid item xs={12} md={6}>
            <Stack>
              <InputLabel required>Booth ID</InputLabel>
              <Select
                fullWidth
                id="boothId"
                name="boothId"
                value={boothId}
                onChange={(e) => {
                  setBoothId(e.target.value);
                }}
                disabled
              >
                {booths.map((row, index) => (
                  <MenuItem value={row.boothId} key={index}>
                    {row.title}
                  </MenuItem>
                ))}
              </Select>
            </Stack>
          </Grid>
          <Grid item xs={12} md={6}>
            <Stack>
              <InputLabel required>Voter ID</InputLabel>
              <TextField
                fullWidth
                id="voterId"
                name="voterId"
                value={voterId}
                onChange={(e) => {
                  setVoterId(e.target.value);
                }}
                disabled
              />
            </Stack>
          </Grid>
          <Grid item xs={12} md={6}>
            <Stack>
              <InputLabel required>Select Gender</InputLabel>
              <Select
                fullWidth
                id="gender"
                name="gender"
                value={gender}
                onChange={(e) => {
                  setGender(e.target.value);
                }}
                disabled
              >
                <MenuItem value={`Male`}>Male</MenuItem>
                <MenuItem value={`Female`}>Female</MenuItem>
                <MenuItem value={`Others`}>Others</MenuItem>
              </Select>
            </Stack>
          </Grid>
          <Grid item xs={12} md={6}>
            <Stack>
              <InputLabel required>Ward Number</InputLabel>
              <TextField
                fullWidth
                id="wardNumber"
                name="wardNumber"
                value={wardNumber}
                onChange={(e) => {
                  setWardNumber(e.target.value);
                }}
                disabled
              />
            </Stack>
          </Grid>

          <Grid item xs={12} md={6}>
            <Stack>
              <InputLabel required>DOB</InputLabel>
              <TextField
                fullWidth
                id="dob"
                name="dob"
                type="date"
                format="dd/MM/yyyy"
                value={dob}
                onChange={(e) => {
                  setDOB(e.target.value);
                }}
                disabled
              />
            </Stack>
          </Grid>
          {whatsappNumber ? (
            <>
              <Grid item xs={12} md={6}>
                <Stack>
                  <InputLabel required>Whatsapp Number</InputLabel>
                  <TextField
                    fullWidth
                    id="whatsappNumber"
                    name="whatsappNumber"
                    value={whatsappNumber}
                    onChange={(e) => {
                      setWhatsappNumber(e.target.value);
                    }}
                    disabled
                  />
                </Stack>
              </Grid>
            </>
          ) : (
            <></>
          )}
          <Grid item xs={12} md={6}>
            <Stack>
              <InputLabel required>Number</InputLabel>
              <TextField
                fullWidth
                id="number"
                name="number"
                value={number}
                onChange={(e) => {
                  setNumber(e.target.value);
                }}
                disabled
              />
            </Stack>
          </Grid>
          {voterView ? (
            <>
              <Grid item xs={12} md={6}>
                <Stack>
                  <InputLabel required>Voter View</InputLabel>
                  <Select
                    fullWidth
                    id="voterView"
                    name="voterView"
                    value={voterView}
                    onChange={(e) => {
                      setVoterView(e.target.value);
                    }}
                    disabled
                  >
                    <MenuItem value={`BJP`}>BJP</MenuItem>
                    <MenuItem value={`Congress`}>Congress</MenuItem>
                    <MenuItem value={`BSP`}>BSP</MenuItem>
                    <MenuItem value={`RPL`}>RPL</MenuItem>
                    <MenuItem value={`IND`}>IND</MenuItem>
                    <MenuItem value={`Other`}>Other</MenuItem>
                  </Select>
                </Stack>
              </Grid>
            </>
          ) : (
            <></>
          )}
          {occupation ? (
            <>
              <Grid item xs={12} md={6}>
                <Stack>
                  <InputLabel required>Occupation</InputLabel>
                  <Select
                    fullWidth
                    id="occupation"
                    name="occupation"
                    value={occupation}
                    onChange={(e) => {
                      setOccupation(e.target.value);
                    }}
                    disabled
                  >
                    <MenuItem value={`Gov Employee`}>Gov Employee</MenuItem>
                    <MenuItem value={`Private Job`}>Private Job</MenuItem>
                    <MenuItem value={`Self Employed`}>Self Employed</MenuItem>
                    <MenuItem value={`Student`}>Student</MenuItem>
                    <MenuItem value={`Farmer`}>Farmer</MenuItem>
                    <MenuItem value={`Housewife`}>Housewife</MenuItem>
                    <MenuItem value={`Other`}>Other</MenuItem>
                  </Select>
                </Stack>
              </Grid>
            </>
          ) : (
            <></>
          )}
          {occupationCity ? (
            <>
              <Grid item xs={12} md={6}>
                <Stack>
                  <InputLabel required>Occupation City</InputLabel>
                  <Select
                    fullWidth
                    id="occupationCity"
                    name="occupationCity"
                    value={occupationCity}
                    onChange={(e) => {
                      setOccupationCity(e.target.value);
                    }}
                    disabled
                  >
                    <MenuItem value={`Home Town`}>Home Town</MenuItem>
                    <MenuItem value={`District`}>District</MenuItem>
                    <MenuItem value={`State`}>State</MenuItem>
                    <MenuItem value={`Out of state`}>Out of state</MenuItem>
                    <MenuItem value={`Other`}>Other</MenuItem>
                  </Select>
                </Stack>
              </Grid>
            </>
          ) : (
            <></>
          )}
          {education ? (
            <>
              <Grid item xs={12} md={6}>
                <Stack>
                  <InputLabel required>Education</InputLabel>
                  <Select
                    fullWidth
                    id="education"
                    name="education"
                    value={education}
                    onChange={(e) => {
                      setEducation(e.target.value);
                    }}
                    disabled
                  >
                    <MenuItem value={`Educated`}>Educated</MenuItem>
                    <MenuItem value={`Uneducated`}>Uneducated</MenuItem>
                    <MenuItem value={`10th Pass`}>10th Pass</MenuItem>
                    <MenuItem value={`12th Pass`}>12th Pass</MenuItem>
                    <MenuItem value={`Graduation`}>Graduation</MenuItem>
                    <MenuItem value={`Post Graduation`}>
                      Post Graduation
                    </MenuItem>
                    <MenuItem value={`Other`}>Other</MenuItem>
                  </Select>
                </Stack>
              </Grid>
            </>
          ) : (
            <></>
          )}
          {caste ? (
            <>
              <Grid item xs={12} md={6}>
                <Stack>
                  <InputLabel required>Caste</InputLabel>
                  <TextField
                    fullWidth
                    id="caste"
                    name="caste"
                    value={caste}
                    onChange={(e) => {
                      setCaste(e.target.value);
                    }}
                    disabled
                  />
                </Stack>
              </Grid>
            </>
          ) : (
            <></>
          )}
          {voted ? (
            <>
              <Grid item xs={12} md={6}>
                <Stack>
                  <InputLabel required>Voted</InputLabel>
                  <Select
                    fullWidth
                    id="voted"
                    name="voted"
                    value={voted}
                    onChange={(e) => {
                      setVoted(e.target.value);
                    }}
                    disabled
                  >
                    <MenuItem value={true}>Yes</MenuItem>
                    <MenuItem value={false}>No</MenuItem>
                  </Select>
                </Stack>
              </Grid>
            </>
          ) : (
            <></>
          )}
          {dead ? (
            <>
              <Grid item xs={12} md={6}>
                <Stack>
                  <InputLabel required>Dead</InputLabel>
                  <Select
                    fullWidth
                    id="dead"
                    name="dead"
                    value={dead}
                    onChange={(e) => {
                      setDead(e.target.value);
                    }}
                    disabled
                  >
                    <MenuItem value={`Yes`}>Yes</MenuItem>
                    <MenuItem value={`No`}>No</MenuItem>
                  </Select>
                </Stack>
              </Grid>
            </>
          ) : (
            <></>
          )}

          <br />
        </Grid>
      </form>
    </MainCard>
  );
}

export default ViewMember;
